.headerContainer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    .title {
        font-weight: 700;
        font-size: 19px;
    }

    .saveButton {
        width: 66px;
        height: 28px;
        background-color: #FF6600;
        border-radius: 100px;
        color: #fff;
        font-size: 18px;
        line-height: 18px;
        font-weight: 500;
        border: none;
        outline: none;
        cursor: pointer;
    }
}

.userWrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    margin-bottom: 20px;

    .coverPhoto {
				// position: relative;
        height: 200px;
        background-color: #aaa;
        margin: 0 1px;
				width: 100%;
				object-fit: cover;
				z-index: 0;
    }
		.photoIcon {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			column-gap: 30px;
			margin-top: 88px;
			left: 45%;
			z-index: 1;

			.uploadIcon,
			.cancelIcon {
				width: 24px;
				height: 24px;
			}
		}

    .userAvatar {
        position: absolute;
        top: 140px;
        left: 14px;

        .avatar {
            width: 120px;
            height: 120px;
            border-radius: 50%;
            background-color: #C4C4C4;
            border: 4px solid #fff;
        }

        .uploadIcon {
            width: 24px;
            height: 24px;
            position: absolute;
            top: 50px;
            left: 50px;
        }
    }

    .userEditInfo {
        display: flex;
        flex-direction: column;
        padding: 80px 15px 50px 15px;

        .inputGroup {
            display: flex;
            flex-direction: column;
            background-color: #F5F8FA;
            border-radius: 4px;
            padding: 5px 15px 4px 15px;
            border-bottom: 2px solid #657786;

            label {
                color: #657786;
                font-size: 15px;
                font-weight: 500;
                line-height: 15px;
                margin-bottom: 4px;
            }

            input, textarea {
                background-color: transparent;
                border: none;
                color: #1C1C1C;
                font-size: 19px;
                font-weight: 500;
                line-height: 27px;
                outline: none;
            }
    
            .textarea {
                // resize: none;
                height: 130px;
            }

        }

        .nameEdit {
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;
        }

        .introductionEdit {
            display: flex;
            flex-direction: column;
        }

        p {
            display: flex;
            justify-content: flex-end;
            color: #657786;
            font-size: 15px;
            font-weight: 500;
            line-height: 21px;
        }

    }

            
    
}
.uploadIcon, .cancelIcon {
    cursor: pointer;
}

.fileInput {
    outline: 1px solid green;
    display: none;
}